import {makeAutoObservable} from "mobx";

class FeedStore{
  //state
  feedPage= 'Feed'
  feedPageText = '  '
  followMidText = '   .    .'

  constructor() {
    makeAutoObservable(this)
  }
  //action
  clickSubscribeing=()=>{
    this.feedPage = "Following"
    this.feedPageText = "0  "
    this.followMidText = '   .    .'
  }
  clickSubscribed=()=>{
    this.feedPage = "Follower"
    this.feedPageText= "0   "
    this.followMidText = '     .'
  }
}

export default FeedStore