import React from "react";

const AdminFilter = (props) => {
  const filterChangeHandler = (event) => {
    props.onChangeFilter(event.target.value);
  };

  return (
    <>
      <div>
        <label>     </label>
        <select value={props.selected} onChange={filterChangeHandler}>
          <option value=".">.</option>
          <option value=" ."> .</option>
        </select>
      </div>
    </>
  );
};

export default AdminFilter;
