import React from "react";
import classes from "../../../css/admin/profile/SupportList.module.css";
import MyProfile from "../MyProfile";

const SupportList = () => {
  return (
    <div id={classes.supportList}>
      <div id={classes.supportListLeft}>
        <MyProfile />
      </div>
      <div id={classes.supportListRight}>
        <div className={classes.mainTitle}></div>
        <div className={classes.supportBlock1}></div>

        <div className={classes.supportBlock2}></div>
      </div>
    </div>
  );
};

export default SupportList;
